import Api from '@/common/api';
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			desc: '', //备注
			goodsId: 0,
			cartIds: '',
			addressId: '',
			memberReceiveAddressList: [],
			cartPromotionItemList: [],
			calcAmount: {
				totalAmount: 0,
				freightAmount: 0,
				promotionAmount: 0,
				payAmount: 0
			},
			addressData: {
				receiveName: '',
				receivePhone: '',
				province: '',
				city: '',
				area: '',
				detailAddr: ''
			}
		};
	},
	async onLoad(option) {
		let data1;
		let data;
		let params = {
			cartIds: option.cartIds,
			type: option.type
		};
		data = await Api.apiCall('get', Api.order.preOrder, params);
		data1 = data.data;
		this.cartIds = option.cartIds;
		let id_nums=data1.id_nums.split(":");
		data1.goods.map(g=>{
			id_nums.find(id_num=>{
				let id_num_Arr= id_num.split("-");
				if(g.id==id_num_Arr[0]){
					let quantity= id_num_Arr[1];
					g.quantity=quantity;
					this.calcAmount.totalAmount+=quantity*g.goodsOriginalPrice;
					this.calcAmount.promotionAmount+=quantity*(g.goodsOriginalPrice-g.goodsPrice);
					this.calcAmount.freightAmount+=g.goodsTransfee;
					return true;
				}
				return false;
			})
			return g;
		})
		this.calcAmount.payAmount=this.calcAmount.totalAmount-this.calcAmount.promotionAmount+this.calcAmount.freightAmount;
		this.cartPromotionItemList = data1.goods;
		//this.calcAmount = data1.calcAmount;
		this.addressData = data1.address;
		if (this.addressData) {
			this.addressId = this.addressData.id;
		}
		this.memberReceiveAddressList = data1.memberReceiveAddressListaddress;

	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {

		numberChange(data) {
			this.number = data.number;
		},
		async submit() {
			if (!this.addressId) {
				this.$api.msg('请选择收货地址');
				return;
			}
			let params = {
				orderType: 1,
				cartIds: this.cartIds,
				addressId: this.addressId,
				content: this.desc
			};
			let data = await Api.apiCall('post', Api.order.bookOrder, params);
			if (data.code==0) {
				let id = data.data;
				let url = '/pages/order/payment/index?order_id=' + id + '&type=1';
				uni.navigateTo({
					url: url
				});
			} else {
				this.$api.msg(data.msg);
			}
		},
		stopPrevent() {}
	}
};
